<template>
  <v-container>
    <v-layout>
      <v-flex xs12 sm6 offset-sm3>
        <div>
          <datepicker :inline="true" :language="ko" :bootstrap-styling="true"></datepicker>
        </div>
        <v-divider style="margin-bottom: 20px; margin-top: 20px"></v-divider>
        <v-card>
          <v-list>
            <v-list-tile
              v-for="item in items"
              :key="item.title"
              @click="test()"
            >
              <v-list-tile-action>
                <v-icon color="pink">person</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title v-text="item.title"></v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import Datepicker from 'vuejs-datepicker'
  import {en, ko} from 'vuejs-datepicker/dist/locale'

  export default {
    name: "Status",
    components: {Datepicker},
    data() {
      return {
        en: en,
        ko: ko,
        items: [
          {title: 'Jason Oner'},
          {title: 'Travis Howard'},
          {title: 'Ali Connors'},
          {title: 'Cindy Baker'}
        ]
      }
    },
    methods: {
      test: function () {
      }
    }
  }
</script>

<style scoped>

</style>



