<template>
  <v-flex xs12 >
    <v-card height="100px"
            class="white--text"
            color="blue-grey"
            @click.native="$emit('select', spot.id)"
    >
      <v-card-text>
        <div class="display-1 text-xs-center">{{spot.name}}</div>
        <p class="headline text-xs-center">{{spot.time}}</p>
      </v-card-text>
    </v-card>
  </v-flex>
</template>

<script>
  export default {
    props: {
      spot: {
        type: Object,
        required: true
      }
    }
  }
</script>



