<template>
  <v-app id="reserve">
    <v-navigation-drawer
      v-model="drawer"
      fixed
      app
    >
      <v-list dense>
        <v-list-tile @click="">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>처음으로</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click="">
          <v-list-tile-action>
            <v-icon>power_settings_new</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title @click="logout()">로그아웃</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar color="primary" dark fixed app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title></v-toolbar-title>
    </v-toolbar>
    <v-container v-if="list_seen" fluid grid-list-lg style="height: 200px">
      <v-layout row wrap>
        <Spot
          v-for="spot in spots"
          :key="spot.id"
          :spot="spot"
          @select="selectSpot"
        />
      </v-layout>
    </v-container>
    <Status></Status>
  </v-app>
</template>

<script>
  import Spot from './Spot.vue'
  import Status from './Status'

  export default {
    components: {Spot, Status},
    name: 'SpotList',
    data: () => ({
      list_seen: false,
      drawer: false,
      spots: []
    }),
    created: function () {
      this.$http.get('/api/exhibition')
        .then(data => this.spots = data.data)
    },
    methods: {
      selectSpot: function (id) {
        alert(id);
      },
      logout: function () {
        this.$store.dispatch('LOGOUT')
          .then(() => this.$router.push('login'))
          .catch(({message}) => this.msg = message)
      }
    }
  }
</script>

<style>
  #reserve {
    margin-top: 60px;
  }
</style>



